@import url('https://fonts.googleapis.com/css?family=Audiowide');

* {
    margin: 0;
    padding: 0;
}

body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: "Audiowide";
}

.manage-page {
    width: 100%;
    height: 100%;
    
}



.manage-data-header {
    color: white;
    text-align: center;
    padding: 1vh;
    font-size: 2vh;
}

.data-grid {
    background-color: #26262723;
    height: 80vh;
    width: 70vh;
    font-size: 4vh;
    margin-left: auto;
    margin-right: auto;
}

.top-buttons-bar{
    display: flex !important;
    width : 100%;
    
}

.data-grid{
    color : white;
    font-family : "Audiowide";
}

.endzone-search-bar-div{
    width : 33%;
    padding : 0;
    margin-top : 1vh;
}

.top-right-buttons{
    width : 66%;
    height : 100%;
    position : relative;
    display : inline-flex;
    justify-content : center;
    
}

.manage-data-divider{
    width : 75%;
    margin : auto;
    padding : 0;
    
}

.table-grid{
    text-align : center;
    color : white;
    height : 75vh;
}

.footer{
    padding : 0;
    margin : 0;
    position : fixed;
    bottom : 0;
}

.alert-div{
    position : absolute;
    top : 10vh;
    left : 0;
    margin : auto;
    width : 100%;
    z-index : 2;
}

@media(max-width : 1200px) and (min-width: 801px){
    .top-buttons-bar{
        display : inline-block !important;
        width: 100%;

    }
    .top-right-buttons{
        width : 100%;
        height : 100%;
        margin-bottom : 2vh;
    }
    .endzone-search-bar-div{
        width : 100%;
        padding : 0;
        margin-top : 1vh;
        
    }
    .table-grid{
        text-align : center;
        color : white;
        height : 62.5vh;
    }
}
@media (max-width: 800px){
    .table-grid{
        text-align : center;
        color : white;
        height : 62.5vh;
    }
    .top-right-buttons{
        width : 100%;
        height : 100%;
        position : relative;
        display : inline-flex;
        justify-content : center;
        margin-bottom : 1vh;
    }
    .endzone-search-bar-div{
        width : 100%;
        padding : 0;
        margin-top : 1vh;
        
    }
    
    .top-buttons-bar{
        display: inline-block !important;
        width : 100%;
    
    }
}











